.structure-scheme {
  display: flex;
  max-width: 1000px;
  margin: auto;
  width: 100%;
  justify-content: space-between;
  text-align: center;
  flex-wrap: wrap;
  padding-top: 100px;
  height: auto !important;
  @media(max-width: $xl){
  
  }
  @media(max-width: $lg){
  
  }
  @media(max-width: $md){
    
  }
  @media(max-width: $sm){
    flex-wrap: nowrap;
    flex-direction: column;
    text-align: left;
  }
  @media(max-width: $xs){
  
  }
  
  @mixin el {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100px;
    border-radius: 25px;
    border: 1px solid;
    padding: 0 25px;
    box-sizing: border-box;
    margin-top: 30px;
    &::before {
      bottom: 100%;
      top: -30px;
      width: 1px;
      background-color: $text;
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      content: '';
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        // top: 0;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
  }
  &::before {
    border: 1px solid;
    border-bottom: none;
    position: absolute;
    top: 50px;
    left: 15%;
    right: 15%;
    height: 80px;
    content: "";
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      top: 49px;
      bottom: 50px;
      left: 0;
      width: 35px;
      border-right: none;
      height: auto;
      border-bottom: 1px solid;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &::after {
    @include el;
    content: '';
    position: absolute;
    width: 100%;
    max-width: calc(100%/3 - 27px);
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    background: $text url(../img/svg/logo-light.svg) center no-repeat;
    background-size: 189px auto;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
    }
    @media(max-width: $sm){
      max-width: 280px;
      right: auto;
      left: 35px;
      width: calc(100% - 30px);
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__el {
    width: calc(100%/3 - 27px);
    position: relative;
    transform: none !important;
    margin-top: 30px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      width: 100%;
      max-width: 315px;
      min-height: 100px;
      padding-left: 35px;
    }
    @media(max-width: $xs){
    
    }
    
    &.open {
      .structure-scheme__toggle {
        &::after {
          transform: rotate(-135deg);
        }
      }
    }
    &:last-of-type {
      .structure-scheme__toggle {
        &::before {
          // display: none;
        }
      }
    }
  }
  &__toggle {
    @include el;
    border-color: $green;
    background-color: $green;
    font-size: 24px;
    gap: 10px;
    justify-content: space-between;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      font-size: 18px;
    }
    @media(max-width: $sm){
      font-size: 24px;
      &::before {
        left: 0;
        width: 35px;
        right: auto;
        height: 1px;
        bottom: auto;
        top: 50px;
      }
    }
    @media(max-width: $xs){
      font-size: 20px;
    }
    
    &::after {
      width: 18px;
      height: 18px;
      border: 1px solid;
      border-top: none;
      border-left: none;
      content: '';
      transform: rotate(45deg) translate(-4px, -4px);
      transition: all 0.3s;
      flex-shrink: 0;
    }
  }
  &__content {
  }
  &__list {
    display: flex;
    flex-direction: column;
    
  }
  &__list-el {
    @include el;
    position: relative;
    
  }
  &__logo {
  }
}





.octagon {
  width: 100px;
  height: 100px;
  background: red;
  position: relative;
}
 
.octagon:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;    
  border-bottom: 29px solid red;
  border-left: 29px solid #eee;
  border-right: 29px solid #eee;
  width: 42px;
  height: 0;
}
 
.octagon:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;    
  border-top: 29px solid red;
  border-left: 29px solid #eee;
  border-right: 29px solid #eee;
  width: 42px;
  height: 0;
}